import React, { useEffect, useState } from "react";
import MenuAppBar from "../navbar";
import { Switch, Route } from "react-router-dom";
import Dashboard from "./dashboard";
import Reports from "./reports";
import Constants from "./../utils/constants";
import ReportView from "./report_view";
import Admin from "./admin";
import AddUser from "./AddUser";
import ErrorScreen from "./auth/errorScreen";
import Login from "./auth/login";
import HomeScreen from "./homescreen";

function MainScreen(props) {
  const [_projectsList, setProjectsList] = useState([]);
  var defaultSelection = sessionStorage.getItem(Constants.SELECTED_PROJECT);
  const [_projectId, setProjectId] = useState(defaultSelection);

  useEffect(() => {
    var temp = JSON.parse(sessionStorage.getItem(Constants.PROJECTS_LIST));
    if (temp == null) temp = [];

    setProjectsList(temp);
    setProjectId(defaultSelection);
  }, []);

  function onProjectChange(e) {
    setProjectId(e.target.value);
  }

  return (
    <div>
      <h3>Main Sxcreen</h3>
      <Switch>
        <Route path="/login">
          <Login />
        </Route>
        <Route path="/">
          <HomeScreen />
        </Route>
      </Switch>
    </div>
  );
}

export default MainScreen;
